body {
    background-color: #6fdef9;
    box-sizing: border-box;
    font-family: Bungee, sans-serif;
    font-style: normal;
    font-weight: 400;
    margin: 0;
    padding: 0;
  }
  .App {
    grid-template-columns: repeat(3, 1fr);
  }
  #score,
  .App {
    display: grid;
    justify-items: center;
  }
  #score {
    align-items: center;
    color: #ffffff;
    font-size: 40pt;
    grid-column: 1/4;
    grid-template-columns: repeat(17, 1fr);
    text-align: center;
    text-shadow: 0 0 10px #00000080;
    width: 100%;
  }
  #minus {
    grid-column: 5/7;
    opacity: 0.7;
    width: 100%;
  }
  #score-number {
    color: #ffffff;
    font-size: 42pt;
    grid-column: 8/11;
    text-shadow: 0 0 10px #00000080;
    width: 100%;
  }
  #plus {
    grid-column: 12/14;
    opacity: 0.7;
    width: 100%;
  }
  .team-image {
    border-radius: 15px;
    box-shadow: 0 0 10px #00000080;
    cursor: pointer;
    margin-top: 20px;
    max-width: 220px;
  }
  #entry,
  .team-image {
    grid-column: 1/4;
    width: 100%;
  }
  
  #entry {
    display: grid;
    justify-content: center;
  }
  .fill-in-blank-input {
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 10px #00000080;
    color: #000000;
    font-size: 18pt;
    height: 70px;
    margin-top: 140px;
    text-align: center;
    width: auto;
  }
  .fill-in-blank-input:focus {
    box-shadow: 0 0 10px #00000080;
    outline: 5px solid #f8d421;
  }
  .best-of-three-options {
    margin-top: 30px;
  }
  .best-of-three-option {
    align-items: center;
    background-color: #ffffff;
    border-radius: 50%;
    box-shadow: 0 0 10px #00000080;
    display: grid;
    font-size: 36pt;
    height: 90px;
    justify-items: center;
    margin-bottom: 20px;
    opacity: 0.3;
    width: 90px;
  }
  .active-best-of-three-option {
    background-color: #319aaf;
    color: #ffffff;
    cursor: pointer;
    opacity: 1;
  }
  .this-or-that-options {
    align-items: center;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    height: 300px;
    justify-items: center;
    margin-top: 30px;
    width: 100%;
  }
  .this-or-that-option {
    background-color: #ffffff;
    border: none;
    border-radius: 10px;
    box-shadow: 0 0 10px #00000080;
    color: #000000;
    font-family: Bungee, sans-serif;
    font-size: 32pt;
    margin: 0;
    opacity: 0.3;
    padding: 30px;
    width: 100%;
  }
  #OR {
    color: #ffffff;
    font-size: 21pt;
  }
  .active-this-or-that-option {
    background-color: #aa62c7;
    color: #ffffff;
    cursor: pointer;
    opacity: 1;
  }
  #types {
    bottom: 0;
    grid-column: 1/4;
    grid-template-columns: repeat(3, 1fr);
    height: 15vh;
    position: absolute;
  }
  #types,
  .type {
    display: grid;
    width: 100%;
  }
  .type {
    align-items: center;
    box-sizing: border-box;
    color: #ffffff;
    font-size: 12pt;
    font-weight: 700;
    height: 100%;
    justify-items: center;
    padding: 10px;
    text-align: center;
    text-shadow: 0 0 10px #00000080;
  }
  .fill-in-blank {
    background-color: #f8d421;
  }
  .best-of-three,
  .fill-in-blank {
    cursor: pointer;
    opacity: 0.3;
    transition: background-color 0.3s ease;
  }
  .best-of-three {
    background-color: #3abfda;
  }
  .this-or-that {
    background-color: #aa62c7;
    cursor: pointer;
    opacity: 0.3;
    transition: background-color 0.3s ease;
  }
  